<template>
  <div class="wrapbox">
      <xworks></xworks>
  </div>
</template>

<script>

  import { mapActions } from 'vuex'

  import xworks from '@/zones/xworks'

  export default {
    components: {
      xworks
    },
    data () {
      return{}
    },
    mounted () {

      let that=this
      let slideId=this.$http.getUrlKey("id")
      this.setActiveSlide(slideId)

      let params = {
        id: slideId
      }

      this.$http.get('/IShower/getProgramInfoById', params, function (data) {

        if (data != null && data != '') {

          let c=JSON.parse(data)
          if(c.pcontent!='') {
            that.setMySlide(JSON.parse(c.pcontent))
          }
        }

      }, function (err) {
        console.log(err)
      })
    },
    methods: {
      ...mapActions([
        'setMySlide',
        'setActiveSlide'
      ]),
    }
  }
</script>

<style>
  .wrapbox {
    margin: 0;
    padding: 0;
    width: 100%;
    heigth:100%;
  }

</style>



