import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Design from '@/pages/design'
import Preview from '@/pages/preview'
import Debug from '@/pages/debug'
import Test from '@/pages/test'

Vue.use(Router)

export default new Router({
  base: '/DesignWeb/',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Design',
      name: 'Design',
      component: Design
    },
    {
      path: '/Preview',
      name: 'Preview',
      component: Preview
    },
    {
      path: '/Debug',
      name: 'Debug',
      component: Debug
    },
    {
      path: '/Test',
      name: 'Test',
      component: Test
    }
  ]
})



