<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <div class="inner"
           :style="[getThisComp.style, animateStyle]" v-html="ServerTime">
      </div>
    </mydrag>
  </div>
</template>
<script>

  import {mapGetters} from 'vuex'
  import mydrag from '@/plugin/mydrag'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
        ServerTime: ''
      }
    },
    mounted() {
      this.myTimer = setInterval(this.getServerTime, 1000);
    },
    beforeDestroy() {
      clearInterval(this.myTimer)
    },
    methods: {
      getServerTime() {

        let that = this

        let baseUrl = "http://" + this.getServerIp + ":" + this.getHttpPort

        this.$http.get(baseUrl + '/IAdapter/getCurrentDatetime2', {}, function (data) {

          if (data != null && data != '') {
            that.ServerTime = data;
          }

        }, function (err) {
          console.log(err)
        })
      }
    },
    computed: {
      ...mapGetters([
        'getCompById', 'getServerIp', 'getHttpPort'
      ]),
      getThisComp: function () {
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      }
    },
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



